import axios from 'axios'; //   axios    HTTP-

const addUser = () => {
    const registerForm = document.getElementById('ff'); //   

    //     
    registerForm.addEventListener('submit', function(event) {
        event.preventDefault(); //     

        //    
        const username2 = document.getElementById('phone5').value;
        const email2 = document.getElementById('emailul5').value;
        const password2 = document.getElementById('password5').value;

        //  POST-   
        axios
            .post('https://better-success-f3e3e81dd8.strapiapp.com/api/auth/local/register', { // URL   
                username: username2, //    
                email: email2, //      
                password: password2 //    
            })
            .then(response => {
                const jwt = response.data.jwt; //  JWT-   
                const userId = response.data.user.id; //      

                //  JWT-    
                window.localStorage.setItem('jwt', jwt);

                //      
                checkOrCreateCart(userId, jwt);

                //      
                const modal = document.getElementById('myModal2');
                modal.style.display = 'none';
            })
            .catch(error => {
                console.log(error); //           
            });
    });
};

//         
const checkOrCreateCart = (userId, jwt) => {
    axios
        .get('https://better-success-f3e3e81dd8.strapiapp.com/api/carts', { // GET-    
            params: {
                'filters[user][id][$eq]': userId //      
            },
            headers: {
                'Authorization': 'Bearer ' + jwt //     JWT-
            }
        })
        .then(response => {
            //    ,  
            if (response.data.data.length === 0) {
                createCartForUser(userId, jwt); //     
            } else {
                console.log('Cart already exists for user:', response.data.data); //      
            }
        })
        .catch(error => {
            console.log(error); //            
        });
};

//       
const createCartForUser = (userId, jwt) => {
    axios
        .post('https://better-success-f3e3e81dd8.strapiapp.com/api/carts', { // POST-     
            data: {
                user: userId //      
            }
        }, {
            headers: {
                'Content-Type': 'application/json', //   Content-Type
                'Authorization': 'Bearer ' + jwt //     JWT-
            }
        })
        .then(response => {
            console.log('Cart created:', response.data); //       
        })
        .catch(error => {
            console.log(error); //             
        });
};

export default addUser; //   addUser
