import axios from 'axios'; //   axios    HTTP-

const accountModal = () => { //   accountModal
    const accountModal = document.getElementById('accountInfoModal'); //    
    const closeAccountModalButton = document.querySelector('.close-account-modal'); //      
    const logoutButton = document.getElementById('logoutButton'); //     
    const accountEmail = document.getElementById('account-email'); //      
    const accountUsername = document.getElementById('account-username'); //      
    const accountButton = document.getElementById('openAccountInfoModal'); //      

    let jwtToken = localStorage.getItem('jwt'); //  JWT-   
    let userData = JSON.parse(localStorage.getItem('userData')); //          JSON  

    //   JWT-      
    if (jwtToken && userData) {
        accountEmail.textContent = `Email: ${userData.email}`; //        
        accountUsername.textContent = `Username: ${userData.username}`; //        
    }

    //          
    accountButton.addEventListener('click', () => {
        accountModal.style.display = 'block'; //    
    });

    //          
    closeAccountModalButton.addEventListener('click', () => {
        accountModal.style.display = 'none'; //    
    });

    //                 
    window.addEventListener('click', (event) => {
        if (event.target == accountModal) {
            accountModal.style.display = 'none'; //         
        }
    });

    //         
    logoutButton.addEventListener('click', () => {
        localStorage.removeItem('jwt'); //  JWT-   
        localStorage.removeItem('userData'); //      
        setTimeout(() => {
            window.location.reload(); //  
        }, 50);
    });
};

export default accountModal; //   accountModal
