const history = () => {
    const accountInfoModal = document.getElementById('accountInfoModal'); //    
    const closeAccountModalButton = document.querySelector('.close-account-modal'); //      
    const accountEmailElement = document.getElementById('account-email'); //       
    const accountUsernameElement = document.getElementById('account-username'); //      
    const ordersContainer = document.getElementById('orders-container'); //    
    const logoutButton = document.getElementById('logoutButton'); //     
    const openAccountModalButton = document.getElementById('openAccountInfoModal'); //      

    let jwtToken = localStorage.getItem('jwt'); //  JWT-   
    let userData = JSON.parse(localStorage.getItem('userData')); //           JSON  

    //   JWT-      
    if (!jwtToken || !userData || !userData.cart || !userData.cart.id) {
        console.error('User data or JWT token not found'); //      ,     JWT-  
        return; //   
    }

    //         
    const displayOrderInfo = (orders) => {
        ordersContainer.innerHTML = ''; //     

        orders.forEach(order => {
            const orderElement = document.createElement('div'); //    
            orderElement.classList.add('order-item'); //      

            orderElement.innerHTML = `
                <h3> ${order.id}</h3>
                <p>:</p>
                <ul>
                    <li>${order.attributes.name}</li>
                </ul>
            `; //  HTML-  

            ordersContainer.appendChild(orderElement); //     
        });
    };

    //         
    closeAccountModalButton.addEventListener('click', () => {
        accountInfoModal.style.display = 'none'; //    
    });

    //     ""
    logoutButton.addEventListener('click', () => {
        localStorage.removeItem('jwt'); //  JWT-   
        localStorage.removeItem('userData'); //      
        window.location.reload(); //    
    });

    //      
    const getOrderInfo = () => {
        const userId = userData.id; //   
        const productRequests = []; //           

        axios.get(`https://better-success-f3e3e81dd8.strapiapp.com/api/orders?userId=${userId}&populate=prods`, { //  GET-      
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${jwtToken}` //     JWT-
            }
        }).then(response => {
            const orders = response.data.data; //    

            //     
            orders.forEach(order => {
                order.attributes.prods.data.forEach(prod => {
                    productRequests.push(axios.get(`https://better-success-f3e3e81dd8.strapiapp.com/api/products/${prod.id}`, { //         
                        headers: {
                            'Content-Type': 'application/json',
                            'Authorization': `Bearer ${jwtToken}` //     JWT-
                        }
                    }));
                });
            });

            //        
            Promise.all(productRequests)
                .then(responses => {
                    responses.forEach((response, index) => {
                        const product = response.data.data; //       

                        //      
                        orders.forEach(order => {
                            order.attributes.prods.data.forEach(prod => {
                                if (prod.id === product.id) {
                                    prod.attributes.name = product.attributes.name; //     
                                }
                            });
                        });
                    });

                    //         
                    displayOrderInfo(orders);
                })
                .catch(error => {
                    console.error('Error fetching product data:', error.response ? error.response.data : error.message); //            
                });
        }).catch(error => {
            console.error('Error fetching order data:', error.response ? error.response.data : error.message); //            
        });
    };

    //          
    openAccountModalButton.addEventListener('click', () => {
        accountEmailElement.textContent = `Email: ${userData.email}`; //         
        accountUsernameElement.textContent = `Phone: ${userData.username}`; //        
        accountInfoModal.style.display = 'block'; //    
        getOrderInfo(); //    
    });
};

export default history; //   history
